* {
  box-sizing: border-box;
}

html {
  font-family: $font-r;
  scroll-behavior: smooth;
  color: $light-text;
}

body {
  margin: 0;
  height: 100vh;
}

img {
  width: 100%;
}

span,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 400;
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
  line-height: 1;
}

button {
  border: none;
  outline: none;
  background: transparent;
  font-family: $font-r;
  cursor: pointer;
  color: $light-text;
  padding: 0;
}

textarea,
input {
  outline: none;
  border: none;
  resize: none;
  max-height: 80px;
  font-family: $font-r;
}

::-moz-selection {
  color: $dark-text;
  background: $light-background;
}
::selection {
  color: $dark-text;
  background: $light-background;
}
