.app {
  height: 100vh;

  header {
    flex-basis: 50px;

    .logo {
      gap: 10px;
      img {
        width: 60px;
        padding: 10px;
      }
      span {
        font-family: runboy-regular;
        font-size: rem(28px);
      }
    }
  }

  main {
    flex: 1;
    gap: 30px;

    .images {
      gap: 30px;
      flex-direction: column;

      @include for-normal-layout {
        @media (min-width: $narrow-breakpoint) {
          max-width: 1200px;
          max-height: 600px;
          flex-direction: row;
        }
      }

      .image-container {
        max-width: 300px;
        max-height: 300px;
        z-index: 50;
        border-radius: 20px;
        overflow: hidden;
        position: relative;
        border: 2px solid #ffffff;

        @include for-normal-layout {
          @media (min-width: $narrow-breakpoint) {
            max-width: 600px;
            max-height: 600px;
          }
        }

        img {
          height: 100%;
        }

        .image-skeleton {
          width: 100%;
          height: 100%;
          line-height: 1;
        }

        .text {
          position: absolute;
          inset: 0;
          color: #fff;
          transition: opacity 0.2s ease;
          background-color: rgba(0, 0, 0, 0.52);
          opacity: 0;
          font-family: roboto-bold;
          gap: 10px;

          &:hover {
            opacity: 1;
          }
        }
      }
    }

    .actions {
      width: 100%;
      max-width: 350px;
      gap: 30px;
      padding: 0% 20px;

      @include for-normal-layout {
        @media (min-width: $narrow-breakpoint) {
          max-width: 500px;
        }
      }

      button {
        font-size: clamp(rem(14px), 2vw, rem(24px));
        font-weight: bold;
        font-family: roboto-bold;
        padding: 12px 12px;
        border-radius: 10px;
        border: 1px solid #fff;
        text-transform: uppercase;
        transition: all 0.2s ease-in-out;
        background: #000;
        flex: 1;

        &:hover {
          background: rgb(121, 121, 121);
          color: rgb(0, 0, 0);
        }
        &:active {
          background: #fff;
          color: #000;
        }
      }
    }
  }
}
